.l-wrap {
    padding-left: 2rem;
    padding-right: 2rem;
    width: 100%;
    min-height: 100%;
    position: relative;
    z-index: 1;
    overflow: hidden;
    display: flex;
    flex-flow: column nowrap;
}
/*----------*/
.l-header {
    margin-left: -2rem;
    margin-right: -2rem;
}
/*----------*/
.l-content {
    padding-top: 5rem;
    padding-bottom: 5rem;
    flex: 1 auto;

    @media (min-width: $breakpoint-tablet) {
        padding-top: 10rem;
        padding-bottom: 10rem;
    }
    @media (min-width: $breakpoint-desktop) {
        padding-top: 15rem;
        padding-bottom: 15rem;
    }

    .content__wrap {
        margin: auto;
        max-width: 120rem;
        width: 100%;
    }
}
/*----------*/
.l-footer {
    margin-left: -2rem;
    margin-right: -2rem;
    background-color: $color-font;
}
/*----------*/
